import uniTag from '@/components/uni-tag.vue';
import mixPulldownRefresh from '@/components/mix-pulldown-refresh';
// import { chGMT } from '@/common/util/date.js';
import articleOperate from '@/components/article-operate';
var ctime = parseInt(Date.now());
const total = 10;
export default {
	components: {
		uniTag,
		mixPulldownRefresh,
		articleOperate,
	},
	data() {
		return {
			hotList:[],
			showHotMask: false,
			tabIndex: 0,
			enableScroll: true,
			showArticleOperate: false,
			articleId: '',
			articleOffsetTop: 0,
			tabs: [
				{
					name: '推荐',
					active: true,
					data: [],
					offset: 0,
					loadingType: 0
				},
				{
					name: '关注',
					active: false,
					data: [],
					offset: 0,
					loadingType: 0
				}
			],
			loadingText: {
				contentdown: '',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			}
		};
	},
	onLoad() {
		this.init();
		
	},
	onShow() {
		this.getHot()
	},
	onHide() {
		this.showHotMask = false;
	},
	methods: {
		getHot(){
			this.api.home.get_hotVip_List(null,res=>{
				console.log(res)
				this.hotList=res.data
			})
		},
		init() {
			if (!this.tabs[this.tabIndex].data.length) {
				uni.showLoading({
					title: '加载中'
				});
			}
			this.tabs[this.tabIndex].loadingType = 0;
			this.tabs[this.tabIndex].offset = 0;
			return new Promise((onok, onno) => {
				if (this.tabIndex === 0) {
					this.api.home.get_recommend_article(
						{
							type: 2,
							ctime: ctime,
							offset: this.tabs[this.tabIndex].offset,
							total: total
						},	
						res => {
							console.log('推荐数据');
							console.log(res);
							this.tabs[this.tabIndex].data = res.data;
							console.log(this.tabs);
							uni.hideLoading();
							onok(res.data);
						}
					);
				} else {
					this.api.home.get_foucs_article(
						{
							type: 1,
							ctime: ctime,
							offset: this.tabs[this.tabIndex].offset,
							total: total
						},
						res => {
							console.log('关注数据');
							console.log(res);
							this.tabs[this.tabIndex].data = res.data;
							uni.hideLoading();
							onok(res.data);
						}
					);
				}
			});
		},
		getMoreArticle() {
			this.tabs[this.tabIndex].offset += total;
			if (this.tabIndex === 0) {
				this.api.home.get_recommend_article(
					{
						type: 2,
						ctime: ctime,
						offset: this.tabs[this.tabIndex].offset,
						total: total
					},
					res => {
						console.log(res);
						if (res.data.length) {
							this.tabs[this.tabIndex].data = this.tabs[this.tabIndex].data.concat(res.data);
							this.tabs[this.tabIndex].loadingType = 0;
						} else {
							this.tabs[this.tabIndex].loadingType = 2;
						}
					},
					err => {
						this.tabs[this.tabIndex].offset -= total;
						this.tabs[this.tabIndex].loadingType = 0;
					}
				);
			} else {
				this.api.home.get_foucs_article(
					{
						type:1,
						ctime: ctime,
						offset: this.tabs[this.tabIndex].offset,
						total: total
					},
					res => {
						console.log(res);
						if (res.data.length) {
							this.tabs[this.tabIndex].data = this.tabs[this.tabIndex].data.concat(res.data);
							this.tabs[this.tabIndex].loadingType = 0;
						} else {
							this.tabs[this.tabIndex].loadingType = 2;
						}
					},
					err => {
						this.tabs[this.tabIndex].offset -= total;
						this.tabs[this.tabIndex].loadingType = 0;
					}
				);
			}
		},
		async changeTab(index) {
			this.tabs.forEach(item => {
				item.active = false;
			});
			this.tabIndex = index;
			if (!this.tabs[this.tabIndex].data.length) {
				await this.init();
			}
			this.tabs[index].active = true;
		},
		changeSwiper(e) {
			this.changeTab(e.target.current);
		},
		loadMore(i) {
			if (!this.tabs[this.tabIndex].data.length) {
				return;
			}
			if (this.tabs[i].loadingType !== 0) {
				return;
			} else {
				this.tabs[i].loadingType = 1;
				this.getMoreArticle();
			}
		},
		hideHotMask() {
			this.showHotMask = false;
		},
		async onPulldownReresh() {
			ctime = parseInt(Date.now()); //刷新时间
			this.tabs[this.tabIndex].offset = 0;
			await this.init();
			this.$refs.mixPulldownRefresh && this.$refs.mixPulldownRefresh.endPulldownRefresh();
		},
		setEnableScroll(enable) {
			if (this.enableScroll !== enable) {
				this.enableScroll = enable;
			}
		},
		showMoreMask(e) {
			console.log(e);
			uni.getSystemInfo({
				success: res => {
					console.log(res.windowHeight);
					if (e.detail.y + 220 > res.windowHeight) {
						this.articleOffsetTop = e.detail.y - 210;
					} else {
						this.articleOffsetTop = e.detail.y + 20;
					}
					this.showArticleOperate = true;
				}
			});
		},
		hideArticleOperate() {
			this.showArticleOperate = false;
		},
		goMore() {
			uni.navigateTo({
				url: '../celebrity/more/more'
			});
		},
		goDetail(id) {
			uni.navigateTo({
				url: '../celebrity/detail/detail?id='+id
			});
		}
	}
};
